body {
  background-color: rgb(10, 103, 4);
  color: white;
  font-size: 15px;
  font-weight: bold;
}

.header {
  font-size:xx-large;
  text-align: center;
}

div {
  padding: 20px 0;
  margin: auto;
  justify-content: center;
  align-items: center;
}

.length-control {
  text-align: center;
  width: 50%;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 3%;
  padding: 10px;
  display: flex;
  font: 2em sans-serif;
}

.display {
  text-align: center;
  margin: auto;
  width: 50%;
  padding: 10px;
  font: 2em sans-serif;
}
.display-1 {
  display: flex;
}
button {
  margin-left: 40px;
  margin-right: 40px;
  align-items: center;
  background-clip: padding-box;
  background-color:  rgba(255, 255, 255, 0.3);
  border: 1px solid transparent;
  border-radius: .25rem;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: system-ui,-apple-system,system-ui,"Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 20px;
  font-weight: 600;
  justify-content: center;
  line-height: 1.25;
  min-height: 3rem;
  padding: calc(.875rem - 1px) calc(1.5rem - 1px);
  position: relative;
  text-decoration: none;
  transition: all 250ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  width: auto;
}
